import { Container, Button, Form, Table } from 'react-bootstrap';
import { useState, useEffect, ChangeEvent, FormEvent } from 'react';
import './Contact.css';

interface FormInputData {
  name: string;
  email: string;
  message: string;
}

export const Contact = () => {
  const contactDetails = {
    address: 'Nrodn technick knihovna, Technick 2710/6, 160 80 Praha 6',
    phone: '158',
    email: '4reactotrokyne@physioreact.achjo',
    openingHours: 'Pondl - Ptek, 8:00 - 18:00',
  };

  const [inputData, setInputData] = useState<FormInputData>({
    name: '',
    email: '',
    message: '',
  });

  const [formSubmitted, setFormSubmitted] = useState<boolean>(false);

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setInputData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    //e.preventDefault();
    localStorage.setItem('formSubmitted', 'true');
    setFormSubmitted(true);
    console.log(inputData);
    setInputData({
      name: '',
      email: '',
      message: '',
    });
  };

  useEffect(() => {
    if (localStorage.getItem('formSubmitted') === 'true') {
      setFormSubmitted(true);
      setTimeout(() => {
        localStorage.removeItem('formSubmitted');
        setFormSubmitted(false);
      }, 5000);
    }
  }, []);

  return (
    <div className='contact-form-body'>
      <Container className='mt-5 form-container'>
        {localStorage.getItem('formSubmitted') && (
          <div className='infoMessage'>Formul byl spn odesln!</div>
        )}
        <h1>Kontaktujte ns</h1>
        <Table className='mt-4'>
          <tbody>
            <tr>
              <td>
                <strong>Adresa</strong>
              </td>{' '}
              <td>{contactDetails.address}</td>
            </tr>
            <tr>
              <td>
                <strong>Telefon</strong>
              </td>{' '}
              <td>{contactDetails.phone}</td>
            </tr>
            <tr>
              <td>
                <strong>E-mail</strong>
              </td>{' '}
              <td>{contactDetails.email}</td>
            </tr>
            <tr>
              <td>
                <strong>Otevrac doba</strong>{' '}
              </td>
              <td>{contactDetails.openingHours}</td>
            </tr>
          </tbody>
        </Table>
        <h1 className='mt-4'>Napite nm</h1>
        <Form onSubmit={handleSubmit} className='mt-4'>
          <Form.Group className='mb-3'>
            <Form.Label htmlFor='contact-name'>Jmno</Form.Label>
            <Form.Control
              type='text'
              name='name'
              id='contact-name'
              placeholder='Vae jmno'
              onChange={handleChange}
            />
          </Form.Group>
          <Form.Group className='mb-3'>
            <Form.Label htmlFor='contact-email'>E-mail</Form.Label>
            <Form.Control
              type='text'
              name='email'
              id='contact-email'
              placeholder='V e-mail'
              onChange={handleChange}
            />
          </Form.Group>
          <Form.Group className='mb-3'>
            <Form.Label htmlFor='your-message'>Zprva</Form.Label>
            <Form.Control
              as='textarea'
              id='your-message'
              name='message'
              placeholder='Vae zprva'
              className='your-message'
              rows={5}
              onChange={handleChange}
            />
          </Form.Group>
          <Button type='submit' className='action-btn mt-3 mb-5'>
            Odeslat
          </Button>
        </Form>
        <div>
          <h4 className='contact-question'>Potebujete poradit?</h4>
          <p className='contact-text'>
            Jsme tu pro Vs! Sta nm zavolat na linku:
            <span className='contact-phone'> 158</span>
          </p>
        </div>
      </Container>
    </div>
  );
};
