import { Link } from 'react-router-dom';
import { ReviewCard } from './ReviewCard';
import { FaqCard } from './FaqCard';
import reviews from './reviews.json';
import faqs from './faqs.json';
import './LandingPage.css';
import { Button, Container, Col, Row } from 'react-bootstrap';
import { useLocation } from 'react-router-dom';
import { useEffect } from 'react';

export const LandingPage = () => {
  const location = useLocation();

  useEffect(() => {
    if (location.state?.scrollToFAQ) {
      const faqSection = document.getElementById('faq');
      if (faqSection) {
        faqSection.scrollIntoView({ behavior: 'smooth' });
      }
    }
  }, [location]);

  return (
    <Container>
      <section className='first-section'>
        <h1 className='logo'>PhysioReact</h1>
        <p className='slogan'>kdy u tlo neme, PhysioReact pome</p>
        <div className='section1-buttons'>
          <Link to='/bookingform'>
            <Button className='action-btn booking-btn-lg'>
              Objednat termn
            </Button>
          </Link>
          <div>
            <Link to='/aboutus'>
              <Button variant='link' className='learn-more-link'>
                Zjistit vce
              </Button>
            </Link>
          </div>
        </div>
      </section>
      <section>
        <h2 className='intro-heading'>Prevence je pl zdrav</h2>
        <article className='intro-text'>
          Vae cesta k pohybov svobod a zdrav zan tady. Jsme tady pro vs,
          abychom vm pomohli doshnout optimlnho zdrav a pohybov kondice.
        </article>
        <div className='section2-buttons'>
          <Link className='services-btn-link' to='/services'>
            <Button className='action-btn services-btn'>Nae sluby</Button>
          </Link>
          <Link to='/aboutus'>
            <Button variant='outline-primary' className='about-us-btn'>
              O ns
            </Button>
          </Link>
        </div>
      </section>

      <section className='how-to-section'>
        <h2 className='how-to-main-heading'>Jak to u ns funguje?</h2>
        <div>
          <div className='step'>
            <div className='step-icon'>1</div>
            <div className='step-content'>
              <h4 className='how-to-heading'>Objednn se na konzultaci</h4>
              <p className='how-to-text'>
                Objednn na prvn konzultaci je snadn a rychl. Mete ns
                kontaktovat telefonicky, e-mailem nebo vyut n online
                rezervan systm na webovch strnkch. Po domluv termnu vm
                zaleme potvrzen a dal potebn informace.
              </p>
            </div>
          </div>
          <div className='step'>
            <div className='step-icon'>2</div>
            <div className='step-content'>
              <h4 className='how-to-heading'>
                {' '}
                Prvn nvtva a vstupn konzultace
              </h4>
              <p className='how-to-text'>
                Pi prvn nvtv vs pivt n ptelsk tm a vyplnte
                krtk zdravotn dotaznk. Pot se setkte s jednm z naich
                zkuench fyzioterapeut, kter provede podrobnou diagnostiku a
                vyslechne vae poteby a cle. Na zklad tchto informac
                sestavme individuln lebn pln pmo pro vs.
              </p>
            </div>
          </div>
          <div className='step'>
            <div className='step-icon'>3</div>
            <div className='step-content'>
              <h4 className='how-to-heading'>
                Pravideln terapie a sledovn pokrok
              </h4>
              <p className='how-to-text'>
                Na zklad vaeho individulnho plnu zahjme terapii.
                Pravideln se budeme setkvat podle domluvenho harmonogramu,
                abychom zajistili, e terapie postupuje sprvn a vy dosahujete
                svch cl. Bhem kad nvtvy budeme sledovat vae pokroky a
                v ppad poteby upravme lebn pln tak, aby vm co nejlpe
                vyhovoval.
              </p>
            </div>
          </div>
        </div>
        <Link className='booking-btn-sm-link' to='/bookingform'>
          <Button className='action-btn booking-btn-sm'>Objednat se</Button>
        </Link>
      </section>

      <section className='faq-section' id='faq'>
        <h2 className='how-to-main-heading faq-heading'>FAQ</h2>
        <Row>
          <Col xs={12} md={8} id='FAQ'>
            {faqs.map((faq) => (
              <FaqCard key={faq.id} faq={faq} />
            ))}
          </Col>
          <Col className='contact-us-section'>
            <h3 className='contact-us-heading'>Mte otzku?</h3>
            <p className='contact-us-text'>Vyplte kontaktn formul</p>
            <Link to='/contact'>
              <Button className='action-btn contact-us-btn'>
                Kontaktujte ns
              </Button>
            </Link>
          </Col>
        </Row>
      </section>

      <section className='client-section'>
        <h2 className='clients-heading'>Co o ns kaj nai klienti</h2>
        <Row>
          {reviews.map((review) => (
            <ReviewCard key={review.id} review={review} />
          ))}
        </Row>
      </section>
    </Container>
  );
};
