import { Container } from 'react-bootstrap';

export const Gdpr = () => {
  return (
    <>
      <Container className='mt-5 '>
        <h1>Informace pro pacienty o zpracovn osobnch daj a GDPR</h1>
        <p>
          podle nazen Evropskho parlamentu a Rady (EU) 2016/679 ze dne 27.
          dubna 2016 o ochran fyzickch osob v souvislosti se zpracovnm
          osobnch daj a o volnm pohybu tchto daj a o zruen smrnice
          95/46/ES (obecn nazen o ochran osobnch daj).
        </p>
      </Container>
      <Container>
        <h2>Sprvce osobnch daj</h2>
        <p>PhysioReact, s.r.o.</p>
        <p>
          Sprvce je poskytovatelem zdravotnch slueb dle zkona . 372/2011
          Sb., o zdravotnch slubch a podmnkch jejich poskytovn, ve znn
          pozdjch pedpis.
        </p>
      </Container>
      <Container>
        <h2>ely zpracovn osobnch daj</h2>
        <p>
          Osobn daje pacient sprvce osobnch daj zpracovv zejmna za
          elem:
        </p>
        <ul>
          <li>poskytovn zdravotnch slueb,</li>
          <li>
            sdlovn daj o zdravotnm stavu Vm a dalm oprvnnm osobm
            (nap. pi nvaznosti pe jinmu poskytovateli zdravotnch slueb
            i pacientem urenm osobm),
          </li>
          <li>
            organizace poskytovn zdravotnch daj (zejmna pi objednvn
            pacient),
          </li>
          <li>
            veden evidence o naich pjmech a vdajch, pijatch platbch a o
            hospodaen, to ve v souladu s pslunmi prvnmi pedpisy.
          </li>
        </ul>
      </Container>
      <Container>
        <h2>Prvn zklad zpracovn osobnch daj</h2>
        <p>
          Prvnm zkladem pro zpracovn osobnch daj jsou pslun prvn
          pedpisy R, zejmna pak zkon . 372/2011 Sb., o zdravotnch slubch
          a obecn nazen o ochran osobnch daj, a to z dvodu plnn
          zvazk ze smlouvy o pi o zdrav, kter dle zkona . 89/2012 Sb.,
          obanskho zkonku ani dle zkona . 372/2011 Sb., o zdravotnch
          slubch nemus bt uzavena psemn, z dvodu nezbytnosti pro ely
          poskytovn zdravotn pe a z dvodu nezbytnosti pro plnn prvnch
          povinnost, kter se na sprvce vztahuj.
        </p>
      </Container>
      <Container>
        <h2>Pjemci osobnch daj</h2>
        <p>
          Pjemci osobnch daj mohou bt v souladu s platnmi prvnmi
          pedpisy v konkrtnch ppadech krom Vs tak jin osoby, jedn se
          zejmna o:
        </p>
        <ul>
          <li>poskytovatele zdravotnch slueb pi nvaznosti pe,</li>
          <li>
            osoby oprvnn nahlet do zdravotnick dokumentace, a to dle
            zkona . 372/2011 Sb., o zdravotnch slubch,
          </li>
          <li>
            dal zpracovatele, a to na zklad smluv o zpracovn osobnch
            daj uzavench v souladu s obecnm nazenm o ochran osobnch
            daj.
          </li>
        </ul>
        <p>Osobn daje pacient nejsou pedvny do zahrani.</p>
      </Container>
      <Container>
        <h2>Doba zpracovn osobnch daj</h2>
        <p>
          Osobn daje obsaen ve zdravotnick dokumentaci jsou zpracovvny v
          souladu s vyhlkou . 98/2012 Sb., o zdravotnick dokumentaci, ve
          znn pozdjch pedpis. Osobn daje zpracovvan pro dal ely
          ve uveden jsou zpracovvny po dobu stanovenou prvnmi pedpisy
          nebo po dobu, po kterou budete nam pacientem, a nsledn po dobu
          pti let pot, co prokazateln nam pacientem pestanete bt
          (prokazateln se v tomto ppad rozum ode dne psemnho oznmen, e
          pestvte bt nam pacientem).
        </p>
      </Container>
      <Container>
        <h2>Prva subjektu daj</h2>
        <p>
          Pi zpracovn osobnch daj mte nsledujc prva tkajc se
          ochrany svch osobnch daj:
        </p>
        <ul>
          <li>prvo poadovat od ns pstup k Vaim osobnm dajm;</li>
          <li>prvo na opravu Vaich osobnch daj, kter zpracovvme;</li>
          <li>
            prvo na omezen zpracovn. Omezen zpracovn znamen, e musme
            Vae osobn daje, u nich bylo zpracovn omezeno, oznait a po
            dobu trvn omezen je nesmme dl zpracovvat s vjimkou jejich
            uloen.
          </li>
          <li>
            prvo na vmaz osobnch daj. Prvo na vmaz osobnch daj se
            vztahuje jen na osobn daje, kter zpracovvme k jinm elm, ne
            je poskytovn zdravotnch slueb. daje, kter o Vs vedeme za
            elem poskytovn zdravotnch slueb (nap. ve zdravotnick
            dokumentaci), vymazat nesmme;
          </li>
          <li>
            prvo na penositelnost daj. Mete dat, abychom Vm Vae osobn
            daje poskytli za elem jejich pedn jinmu sprvci osobnch
            daj, nebo abychom je sami pedali jinmu sprvci osobnch daj.
          </li>
          <li>
            prvo podat stnost u dozorovho adu, a to v ppad, e se
            domnvte, e zpracovnm osobnch daj dochz k poruen prvnch
            pedpis o ochran osobnch daj. Stnost mete podat u
            dozorovho adu v mst svho obvyklho bydlit, mst vkonu
            zamstnn nebo v mst, kde dolo k dajnmu poruen. V esk
            republice je dozorovm adem ad na ochranu osobnch daj, Pplk.
            Sochora 27, 170 00 Praha 7, www.uoou.cz.
          </li>
          <li>
            Prvo vznst nmitku proti zpracovn v ppad, e Vae osobn
            daje zpracovvme pro ely oprvnnch zjm naich nebo nkoho
            dalho (prvn zklady zpracovn), mte kdykoliv prvo vznst
            proti takovmu zpracovn nmitku. Nmitku mete vznst na na
            adrese uveden v bodu 1. Pokud takovou nmitku vznesete, budeme
            oprvnni v takovm zpracovn pokraovat jen tehdy, pokud prokeme
            zvan oprvnn dvody pro zpracovn, kter pevauj nad Vaimi
            zjmy nebo prvy a svobodami, a dle tehdy, pokud pjde o zpracovn
            nezbytn pro uren, vkon nebo obhajobu prvnch nrok.
          </li>
        </ul>
      </Container>
      <Container className='mb-5'>
        <h2>Povinn zpracovn a povinnost poskytnout osobn daje</h2>
        <p>
          Zpracovn Vaich osobnch daj pro ely poskytovn zdravotnch
          slueb je zkonnm poadavkem. Neposkytnutm Vaich osobnch daj
          me znamenat, e Vm nebudeme moci poskytnout zdravotn sluby, m
          me dojt k pokozen Vaeho zdrav i pmmu ohroen ivota ( 41
          odst.1 psm. d) zkona . 372/2011 Sb., o zdravotnch slubch a
          podmnkch jejich poskytovn). Povinnost poskytnout osobn daje
          pacienta se tk i jeho zkonnho zstupce nebo opatrovnka ( 41
          odst. 2 zkona . 372/2011 Sb., o zdravotnch slubch a podmnkch
          jejich poskytovn).
        </p>
      </Container>
    </>
  );
};
