import { Container } from 'react-bootstrap';
import { EmployeeCard } from './EmployeeCard';
import employees from './employees.json';

interface Employee {
  id: number;
  name: string;
  specialization: string;
  education: string;
  freeTime: string;
  others: string;
  alt: string;
}

export const Aboutus = () => {
  return (
    <>
      <Container className='mt-5'>
        <h1>O ns</h1>
        <p className='mt-4'>
          Fyzioterapeutick klinika PhysioReact se specializuje na komplexn
          rehabilitaci a pi o pohybov apart. Nae multidisciplinrn tm
          tvo vysoce kvalifikovan fyzioterapeuti s dlouholetou prax, kte
          se zamuj na individuln pstup ke kadmu pacientovi.
        </p>
      </Container>
      <Container>
        <h2>Nae filozofie</h2>
        <p>
          Vme v holistick pstup ke zdrav, kde se spojuj modern
          fyzioterapeutick techniky s osobn p a porozumnm k potebm
          kadho jednotlivce. Nam clem je nejen zmrnn bolesti, ale i
          poslen tla a prevence budoucch problm.
        </p>
      </Container>
      <Container>
        <h2>N tm</h2>
        <p>
          N tm tvo tyi zkuen fyzioterapeuti, kte jsou zde pro vs
          kad den. Kad z naich specialist m sv uniktn zamen a
          dovednosti, aby vm mohl poskytnout tu nejlep pi.
        </p>
      </Container>
      {employees.map((employee: Employee) => (
        <EmployeeCard key={employee.id} employee={employee} />
      ))}
    </>
  );
};
