import React, { useState, useEffect } from 'react';
import { Container, Row } from 'react-bootstrap';
import ServiceCard from './ServiceCard';
import servicesData from '../../assets/services.json'; // import JSON souboru
import './Services.css';

interface Service {
  id: number;
  name: string;
  description: string;
}

export const Services = () => {
  const [services, setServices] = useState<Service[]>([]); // explicitn definice typu Service[] pro services

  useEffect(() => {
    // Simulace naten dat (nap. z API)
    setServices(servicesData);
  }, []);

  return (
    <Container className='mt-5 services-container'>
      <h1>Nae sluby</h1>
      <p className='mt-4'>
        Jsme tady proto, abychom vm pomohli doshnout optimln pohyblivosti a
        pohodl ve vaem ivot. Nabzme irokou klu specializovanch slueb,
        kter jsou navreny tak, aby vyhovly individulnm potebm kadho
        klienta.
      </p>

      <Row className='service-list'>
        {services.map((service) => (
          <ServiceCard
            key={service.id}
            title={service.name}
            description={service.description}
          />
        ))}
      </Row>
    </Container>
  );
};

export default Services;
